import React from 'react';
import moment from 'moment';
import { Alert, Popconfirm } from 'antd';
import { BtnEdit, BtnDelete } from 'components/common/button';

export default ({ yearEduPlan, onClickBtnEdit, handleDeleteYearEduPlan }) => {
  if (!yearEduPlan) {
    return null;
  }

  return (
    <div style={{ marginTop: 20 }}>
      <Alert
        type="info"
        message={
          <div>
            <span style={{ fontSize: '14px' }}>
              Kế hoạch đào tạo năm học <strong>{moment(yearEduPlan.fromDate).year()}</strong> -
              <strong>{moment(yearEduPlan.toDate).year()}</strong> (
              {moment(yearEduPlan.fromDate).format('DD/MM/YYYY')} -{' '}
              {moment(yearEduPlan.toDate).format('DD/MM/YYYY')})
              <div
                style={{
                  float: 'right',
                  marginTop: '-3px',
                }}
              >
                <BtnEdit
                  onClick={onClickBtnEdit}
                  style={{
                    height: '28px',
                    marginLeft: '10px',
                    maxWidth: '30px',
                    lineHeight: '28px',
                  }}
                />

                <Popconfirm
                  title="Bạn chắc chắn muốn xóa?"
                  onConfirm={() => handleDeleteYearEduPlan(yearEduPlan)}
                  okText="Xóa"
                  cancelText="Hủy"
                >
                  <BtnDelete
                    style={{
                      height: '28px',
                      marginLeft: '10px',
                      maxWidth: '30px',
                      lineHeight: '28px',
                    }}
                  />
                </Popconfirm>
              </div>
            </span>
          </div>
        }
      />
    </div>
  );
};



